.header {
    background: url(../img/bg/main-bg.jpg) center center/cover no-repeat;
    min-height: 700px;
    padding-top: 35px;
    color: #fff;
    position: relative;
    &__slide {
        display: flex;
        justify-content: space-between;
        align-items: start;
        cursor: default;
        position: relative;
    }
    &__menu {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        margin-right: auto;
    }
    .menu_items {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__link {
        color: #fff;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 13px;
        margin-right: 45px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__carousel {
        margin-top: 135px;
    }
    &__descr {
        width: 530px;
        padding-top: 130px;
    }
    &__title {
        font-size: 42px;
        margin-bottom: 28px;
    }
    &__text {
        font-weight: 300;
        font-size: 24px;
        margin-bottom: 40px;
    }
    &__img {
        position: absolute;
        bottom: 55px;
        right: -10px;
    }
    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.glide__track {
    height: calc(700px - 65px);
}   
.glide__slides {
    height: 100%;
}
.glide__slides {
    cursor: default;
}
.glide__slide {
    margin-top: 55px;
}
.glide__arrow {
    border: none;
    background: none;
    box-shadow: none;
    img {
        width: 6px;
        height: 10px;
    }
}
.glide__arrow--left {
    left: 3.5em;
}
.glide__arrow--right {
    right: 3.5em;
}
.glide__bullets {
    cursor: default;
}
.glide__bullet {
    background: none;
    border: 1px solid #fff;
    margin: 0 1px;
    &:focus {
        background-color: #fff;
    }
}
.glide__bullet--active {
    background-color: #fff;
}
@media screen and (max-width: 992px) {
    .header {
        &__link {
            font-size: 13px;
            margin-right: 25px;
        }
        &__img {
            display: none;
        }
    }
}
@media screen and (max-width: 768px) {
    .header {
        &__menu {
            flex-direction: column;
            justify-content: center;
            img {
                margin-right: 0;
            }
            & > * {
                margin: 10px 0;
            }
        }
        &__descr {
            padding-top: 50px;
        }
        &__title {
            font-size: 30px;
        }
        &__img {
            display: none;
        }
    }
    .glide__arrow {
        display: none;
    }
    .glide__bullet {
        width: 14px;
        height: 14px;
        margin: 0 5px;
        border-radius: 20%;
    }
}
@media screen and (max-width: 425px) { 
    .header {
        &__descr {
            width: 320px;
            padding-top: 25px;
        }
    }
}