.carousel {
    min-height: 890px;
    background: url(../img/bg/carousel/carousel-bg.jpg) center center/cover no-repeat;
    padding-bottom: 80px;
    padding-top: 80px;
    &__menu {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 840px;
        margin: 0 auto;
        margin-top: 32px;
        border: 1px solid #fff;
        height: 65px;
        list-style-type: none;
        padding-left: 28px;
    }
    &__item {
        display: flex;
        align-items: center;
        border-right: 2px solid #8b9391;
        padding-right: 28px;
        height: 50px;
        &:last-child {
            border-right: none;
        }
    }
    &__link {
        font-size: 14px;
        color: #fff; 
        transition: .2s all;
        &:hover {
            color: $main_color;
            text-decoration: none;
        }
    }
    &__glide {
        img {
            display: block;
            margin: 0 auto;
        }
    }
    .glide__bullets {
        display: none;
    }
}
.glide__arrow_large {
    img {
        width: 18px;
        height: 59px;
    }
}
.glide__arrow--left {
    left: 3em;
}
.glide__arrow--right {
    right: 3em;
}
@media screen and (max-width: 1200px) {
    .carousel{
        &__glide {
            img {
                width: 100%;
                display: block;
                margin: auto;
            }
        }
        .glide__arrow--left {
            left: 0;
        }
        .glide__arrow--right {
            right: 0;
        }
    }
}
@media screen and (max-width: 992px) {
    .carousel {
        min-height: 600px;
        &__menu {
            width: 700px;
            li {
                padding-right: 15px;
            }
        }
    }
}
@media screen and (max-width: 768px) {
    .carousel {
        min-height: 600px;
        &__menu {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 50%;
            height: auto;
            padding-left: 0;
            li {
                border-right: none;
                border-bottom: 2px solid #8b9391;
                width: 100%;
                justify-content: center;
                padding-right: 0;
                &:last-child {
                    border-bottom: none;
                }
                &:hover {
                    color: #fff;
                }
            }
        }
        .glide__slide {
            display: flex;
            align-items: center;
            height: 400px;
        }
        .glide__bullets {
            display: block;
            margin-bottom: 100px;
        }
    }
}