.advantage {
    min-height: 1330px;
    padding-bottom: 80px;
    padding-top: 80px;
    &__wrapper {
        max-width: 1140px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    &__item {
        width: 50%;
        height: 355px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: center center/cover no-repeat;
        &_1 {
            background-image: url(../img/bg/advantages/1.jpg);
        }
        &_2 {
            background-image: url(../img/bg/advantages/2.jpg);
        }
        &_3 {
            background-image: url(../img/bg/advantages/3.jpg);
        }
        &_4 {
            background-image: url(../img/bg/advantages/4.jpg);
        }
        &_5 {
            background-image: url(../img/bg/advantages/5.jpg);
        }
        &_6 {
            background-image: url(../img/bg/advantages/6.jpg);
        }
    }
    &__descr {
        font-size: 23px;
        color: #fff;
        background-color: rgba(0, 0, 0, .5);
        height: 155px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 0 100px;
    }
    .title {
        margin-bottom: 45px;
    }
}
@media screen and(max-width: 992px) {
    .advantage {
        &__descr {
            height: 200px;
        }
    }
}
@media screen and(max-width: 576px) {
    .advantage {
        &__wrapper {
            flex-direction: column;
        }
        &__item {
            width: 100%;
        }
        &__descr {
            padding: 0 30px;
        }
    }
}
@media screen and(max-width: 425px) {
    .title {
        font-size: 24px;
    }
    .title_mini {
        width: 300px;
    }
}