.footer {
    min-height: 500px;
    background-color: #0082a1;
    color: #fff;
    padding-top: 45px;
    padding-bottom: 45px;
    &__wrapper {
        display: flex;
        align-items: start;
        justify-content: space-between;
    }
    &__about-company {
        width: 33%;
    } 
    &__list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        a {
            color: #fff;
            font-size: 14px;
            font-weight: 300;
        }
    }
    &__title {
        font-weight: 700;
        color: #fff;
    }
    &__img {
        margin-top: 20px;
    }
    &__descr {
        font-weight: 300;
        margin-top: 23px;
    }
    hr {
        margin: 44px 0 37px 0;
        border-color: #4da8bd;
        height: 0;
    }
    &__copyrights {
        font-size: 13px;
        font-weight: 300;
        text-align: center;
    }
}
@media screen and(max-width: 768px) {
    .footer {
        &__wrapper {
            flex-direction: column;
        }
        &__list {
            width: 100%;
            justify-content: center;
            align-items: center;
            margin: 15px 0;
        }
    }
}