.comparison {
    min-height: 895px;
    padding-top: 80px;
    padding-bottom: 80px;
    &__item {
        width: 360px;
        height: 255px;
        background: right -20% bottom -30% no-repeat;
        border: 1px solid #ececec;
        text-align: center;
        position: relative;
        &_question {
            background-image: url(../img/comparison/question.webp);
        }
        &_correct {
            background-image: url(../img/comparison/correct.webp);
        }
        img {
            display: block;
            margin: 0px auto;
        }
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 50px 0 37px 0;
        &_transition {
            flex-direction: column;
        }
    }

    &__title {
        font-size: 48px;
        font-weight: 700;
        line-height: 46px;
        margin-top: 35px;
    }
    &__descr {
        font-size: 18px;
        width: 260px;
        text-align: center;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 25px;
        &_light {
            font-weight: 300;
        }
        &_bold {
            font-weight: 700;
        }
    }
}
@media screen and (max-width: 768px) {
    .comparison {
        &__wrapper {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        &__item {
            width: 320px;
            margin: 10px 0;
        }
    }
}