  .header__menu {
    flex-direction: column;
    justify-content: center;
  }

  .header__menu img {
    margin-right: 0;
  }

  .header__menu > * {
    margin: 10px 0;
  }

  .header__descr {
    padding-top: 50px;
  }

  .header__title {
    font-size: 30px;
  }

  .header__img {
    display: none;
  }

  .glide__arrow {
    display: none;
  }

  .glide__bullet {
    width: 14px;
    height: 14px;
    margin: 0 5px;
    border-radius: 20%;
  }

  .comparison__wrapper {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .comparison__item {
    width: 320px;
    margin: 10px 0;
  }

  .carousel {
    min-height: 600px;
  }

  .carousel__menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: auto;
    padding-left: 0;
  }

  .carousel__menu li {
    border-right: none;
    border-bottom: 2px solid #8b9391;
    width: 100%;
    justify-content: center;
    padding-right: 0;
  }

  .carousel__menu li:last-child {
    border-bottom: none;
  }

  .carousel__menu li:hover {
    color: #fff;
  }

  .carousel .glide__slide {
    display: flex;
    align-items: center;
    height: 400px;
  }

  .carousel .glide__bullets {
    display: block;
    margin-bottom: 100px;
  }

  .versions {
    flex-direction: column;
  }

  .versions__display {
    width: 100%;
  }

  .versions__title {
    font-size: 24px;
  }

  .versions__text {
    font-size: 16px;
  }

  .footer__wrapper {
    flex-direction: column;
  }

  .footer__list {
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 15px 0;
  }
}

@media screen and (max-width: 576px) {
  .advantage__wrapper {
    flex-direction: column;
  }

  .advantage__item {
    width: 100%;
  }

  .advantage__descr {
    padding: 0 30px;
  }
}

@media screen and (max-width: 425px) {
  .header__descr {
    width: 320px;
    padding-top: 25px;
  }

  .title {
    font-size: 24px;
  }

  .title_mini {
    width: 300px;
  }
}