section, header, footer {
    padding: 0 15px;
}
@media (min-width: 575.98px){
    section, header, footer {
        padding: 0 calc(50% - 250px);
    }
}

@media (min-width: 767.98px){
    section, header, footer {
        padding: 0 calc(50% - 340px);
    }
}

@media (min-width: 991.98px) {
    section, header, footer {
        padding: 0 calc(50% - 460px);
    }
}

@media (min-width: 1199.98px){
    section, header, footer {
        padding: 0 calc(50% - 570px);
    }
}
.btn {
    width: 241px;
    height: 60px;
    background-color: $main_color;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
    border: none;
    transition: .3s all;
    transform: translateY(0);
    border-radius: 3px;
    cursor: pointer;
    &:hover {
        box-shadow: 3px 3px 20px $main_color;
        transform: translateY(-10%);
    }
    &:active {
        transform: translateY(0);
    }
}
.versions__block {
    width: 167px;
    height: 167px;
    background-color: $main_color;
    opacity: 0.5;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    &_cloud {
        right: 45px;
    }
    &_server {
        left: 45px;
    }
}
.title {
    font-size: 30px;
    font-weight: 900;
    color: #fff;
    margin: 0 auto;
    text-align: center;
    &_mini {
        width: 376px;
    }
    &_dark {
        color: $title_dark_color;
    }
}