.versions {
    min-height: 580px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 0;
    &__header {
        position: absolute;
        z-index: 10;
        top: 79px;
        text-align: center;
    }
    &__display {
        background: center center/cover no-repeat;
        height: 580px;
        width: 50%;
        display: flex;
        align-items: center;
        position: relative;
    }
    &__cloud {
        background-image: url(../img/bg/variants/1.jpg);
        text-align: right;
        &_wrapper {
            padding-right: 80px;
        }
    }
    &__server {
        background-image: url(../img/bg/variants/2.jpg);
        text-align: left;
        &_wrapper {
            padding-left: 80px;
        }
    }
    &__descr {
        position: relative;
        z-index: 5;
    }
    &__title {
        color: #fff;
        font-size: 30px;
        font-weight: 900;
        text-transform: uppercase;
    }
    &__text {
        color: #fff;
        font-size: 18px;
        font-weight: 300;
    }
}
@media screen and(max-width: 768px) {
    .versions {
        flex-direction: column;
        &__display {
            width: 100%;
        }
        &__title {
            font-size: 24px;
        }
        &__text {
            font-size: 16px;
        }
    }
}
@media screen and(max-width: 425px) {
    .versions {
        &__display {
        }
    }
}