.country {
    padding-bottom: 20px;
    padding-top: 20px;
    min-height: 85px;
    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__item {
        display: flex;
        justify-content: center;
        align-items: start;
        margin-right: 33px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__descr {
        display: flex;
        justify-content: center;
        align-items: start;
        flex-direction: column;
        font-size: 12px;
        font-weight: 300;
        margin-left: 11px;
    }
    &__title {
        font-weight: 700;
        margin-bottom: 8px;
    }
    &__phone {
        margin-top: 5px;
        &:first-child {
            margin-top: 0;
        }
    }
    &__email {
        order: 2;
        margin-top: 5px;
    }
    
    a:hover {
        color: #000;
        text-decoration: underline;
    }
}
@media screen and (max-width: 1200px) {
    .country {
        &__wrapper {
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
        }
        &__item {
            justify-content: start;
            width: 300px;
        }
    }
}
@media screen and (max-width: 768px) {
    .country {
        &__wrapper {
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
        }
        &__item {
            justify-content: start;
            width: 250px;
            margin-right: 0;
        }
    }
}